<template>
    <div>
        <mhead-top titleHead="我的评价" lfIcon="el-icon-arrow-left icon" lfUrl="/userCenter/index/index" rgIcon="el-icon-s-home icon" rgUrl="/home"></mhead-top>
        <div class="mBg">
            <div class="mu-me-box">
                <div v-if="list.length==0" class="f16 tc mt30 color6">暂无评论</div>
                <div v-else class="mu-me-inbox" v-for="(item,index) in list" :key="index">
                    <div class="mu-me-top">
                        <div class="star flex f-nowrap">
                            <span class="starTit">评分</span>
                            <div class="starInfo">
                                <el-rate
                                        class="ml10"
                                        v-model="item.goods_rank"
                                        disabled
                                        show-score
                                        text-color="#ff9900">
                                </el-rate>
                            </div>
                            <el-link class="delComment" icon="el-icon-delete" @click="delComment(item.comment_id)">删除</el-link>
                        </div>
                    </div>
                    <div class="time f14 color9">{{item.create_time}}</div>
                    <div class="mu-me-text">
                        {{item.content}}
                    </div>
                    <div class="mu-me-pic">
                        <el-image class="meImg" v-for="(i,cindex) in item.photos" :src="i" :key="cindex" fit="cover" :preview-src-list="item.photos"></el-image>
                    </div>
                    <div class="mue-item" @click="toDetail(item.specID)">
                        <div class="mue-pic">
                            <el-image class="mue-image" :src="item.goodsPhotos" fit="cover"></el-image>
                        </div>
                        <div class="mue-info">
                            <div class="mue-name">{{item.goods_name}}</div>
                            <div class="mue-spec">
                                <span>规格：{{item.spec}}{{item.spec_unit}}</span>
                            </div>
                            <div class="mue-price">
                                <span>￥{{item.price_cn}}/{{item.supply_unit}}</span>
                            </div>
                        </div>
                    </div>

                </div>

            </div>

        </div>
    </div>
</template>


<script>
    import mheadTop from '../../../components/mobile/headTop';
    import {myComment,delComment} from '../../../service/api'
    export default {
        name: 'mMyEvaluate',
        data(){
            return{
                list: [],
            }
        },
        components:{
            mheadTop
        },
        mounted(){
            this.initData();
        },
        computed:{


        },
        methods:{
            async initData(){
                // 获取列表
                var res = await myComment();
                this.list = res;
                console.log(res);
            },
            toDetail(spec_id){
                this.$router.push({path:"/good/detail",query:{specifications_id:spec_id}});
            },
            async delComment(comment_id){
                this.$confirm('你确定要删除该评论吗?', '', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(async ()=> {
                    var res = await delComment(comment_id);
                    if(res.status==12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.initData();
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });
            }

        },
        created(){


        },

    }

</script>

<style lang="scss">
    @import '../../../style/mixin';
    body{
        height: 100%;
    }
    .mBg{
        background-color:$bggray;
        display: block;
        width:100%;
        height: 100%;
        min-height: px2rem(1140);
        padding:px2rem(90) 0 px2rem(104) 0;
    }
    .mu-me-box{
        margin-top:px2rem(20);
    }
    .mu-me-inbox{
        background-color: #fff;
        padding:0 px2rem(20) px2rem(20) px2rem(20);
        margin-top:px2rem(20);
    }
    .mu-me-top{
        padding:px2rem(20) 0 0 0;
        width:100%;
    }
    .mu-me-pic{
        padding-top:px2rem(20);
        display:flex;
        flex-wrap: nowrap;
    }
    .meImg{
        width:px2rem(126);
        height: px2rem(126);
    }
    .starTit{
        width:px2rem(70);
        flex-shrink: 0;
    }
    .delComment{
        color:#999;

        flex-shrink: 0;
        font-size: px2rem(24);
    }
    .starInfo{
        flex-shrink: 1;
        width:100%;
        margin-top:px2rem(4);
    }

    .time{
        width:100%;
        font-size: px2rem(24);
    }


    /*产品*/
    .mue-item{
        background: #f5f5f5;
        padding:px2rem(10);
        margin-top:px2rem(10);
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-around;
    }
    .mue-pic{
        width:px2rem(124);
        height: px2rem(124);
        padding-right:px2rem(14);
    }
    .mue-image{
        width:px2rem(124);
        height: px2rem(124);
        object-fit: contain;
        background-color: #f2f2f2;
    }
    .mue-info{
        width:100%;
    }
    .mue-name{
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        color:#333;
        line-height: px2rem(46);
        min-width: px2rem(460);
    }
    .mue-spec{
        font-size:px2rem(22);
        color:#999;
        display: block;
        flex-wrap: nowrap;
        height: px2rem(40);
        line-height: px2rem(40);
    }
    .mue-spec span{
        display: block;
        float:left;
    }
    .mue-spec .icon{
        font-size:px2rem(22);
        color:#999;
        height: px2rem(40);
        line-height: px2rem(40);
        padding-left:px2rem(6);
        float:left;
    }
    .mue-price{
        font-size:px2rem(22);
        color:#666;
        display: block;
        height: px2rem(40);
        line-height: px2rem(40);
    }



</style>



